$('.js-header').each((index, element) => {
	let $component = $(element);
	let $showMenuButton = $component.find('.js-menu-button');
	let $mainMenu = $(document.body).find('.js-main-menu');
	let menuToggleBool = false;

	$(window).on('scroll', () => {
		$component.toggleClass('small', pageYOffset > 0);
	});

	$showMenuButton.on('click', (e) => {
		if (menuToggleBool) {
			$(e.target).stop(true, true).removeClass('active');
			$mainMenu.stop(true, true).trigger('menu:hide');
		} else {
			$(e.target).stop(true, true).addClass('active');
			$mainMenu.stop(true, true).trigger('menu:show');
		}

		menuToggleBool = !menuToggleBool;
	});
});
